.aboutme-container {
    display: grid;
    grid-template-columns: 60% auto;
    grid-gap: 10px;
    padding: 10px;
}

.aboutme-desc
{
    display: grid;
    align-items: center;
    align-content: right;
    text-align: right;
    box-sizing: border-box;
    font-weight: 600;
    padding-right: 25px;
    line-height: 25px;
    color: black;
}

.blue-highlight
{
    color: rgb(14, 49, 206);
    text-shadow: 0 0 0;
    font-weight: bold;
}

.green-highlight
{
    color: rgb(0, 184, 0);
    text-shadow: 0 0 0;
    font-weight: bold;
}

.gold-highlight
{
    color: rgb(255, 136, 0);
    text-shadow: 0 0 0;
    font-weight: bold;
}

.aboutme-profpic
{
    padding: 20px;
    overflow: hidden;
    min-width: 250px;
    min-height: 250px;
    border-radius: 5px;
    box-shadow: 0px 3px 5px gray;
}