.skills-container
{
    display: flex;
    grid-template-areas: 
    'header header header'
    'skill1 skill2 skill3'                        
    ;
    grid-gap: 10px;
    padding: 10px;
    text-align: right;
}

.skills-header
{
    grid-area: header;
    font-size: xx-large;
    font-weight: bold;
}

.skill-web
{
    text-align: justify;
    grid-area: skill1;
}

.skill-desktop
{
    text-align: justify;
    grid-area: skill2;
}

.skill-ds 
{
    text-align: justify;
    grid-area: skill3;
}