@keyframes color-change {
    0% 
    {
        background-position: 0%
    }
    
    100%
    {
        background-position: 400%
    }
}

.landing-container
{
    display: grid;
}

.color-highlight
{
    margin-left: 40%;
    font-weight: bold;
    letter-spacing: 5px;
    font-size: 550%;
    color: white;
    text-shadow: 0px 0px 15px rgb(161, 161, 161);
    padding-left: 25px;
    padding-right: 25px;
    width: 200px;
    align-self: center;
    align-items: center;
    align-content: center;
    background: linear-gradient(90deg,rgb(248, 137, 137),yellow,rgb(73, 240, 73),rgb(95, 180, 230),rgb(248, 137, 137));
    background-size: 400%;
    animation-name: color-change;
    animation-duration: 8s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

