body 
{
    margin: 0;
    padding: 0;
    font-family: consolas;
}

.timeline
{
    position: relative;
    margin: 50px auto;
    width: 1000px;
    padding: 40px 0;
    box-sizing: border-box;
}

.timeline ul
{
    margin: 0;
    padding: 0;
}

.timeline ul li
{
    list-style: none;
    line-height: normal;
    position: relative;
    width: 50%;
    padding: 20px 40px;
    box-sizing: border-box;
}

.timeline ul li:nth-child(odd)
{
    float: left;
    text-align: right;
    clear: both;
}

.timeline ul li:nth-child(even)
{
    float: right;
    text-align: left;
    clear: both;
}

.timeline::before{
    top: 5%;
    left: 50%;
    width: 2px;
    height: 80%;
    content: '';
    position: absolute;
    background-color: #c5c5c5;
}

.content 
{
    padding-bottom: 20px;
    min-height: 250px;
}

.timeline ul li:nth-child(odd)::before
{
    content: '';
    position: absolute;
    top: 24px;
    right: -6px;
    width: 10px;
    height: 10px;
    background: rgb(33, 233, 50);
    border-radius: 50%;
    box-shadow: 0 0 0 3px rgba(33, 233, 50, 0.2);
}

.timeline ul li::before
{
    animation: 2s fadeanim linear infinite;
}

@keyframes fadeanim 
{
    0%
    {
        box-shadow: 0 0 0 3px rgba(33, 233, 50, 0);
    }

    50%
    {
        box-shadow: 0 0 0 3px rgba(33, 233, 50, 0.5);
    }

    100%
    {
        box-shadow: 0 0 0 3px rgba(33, 233, 50, 0);
    }
}

.timeline ul li:nth-child(even)::before
{
    content: '';
    position: absolute;
    top: 24px;
    left: -4px;
    width: 10px;
    height: 10px;
    background: rgba(33, 233, 50, 1);
    border-radius: 50%;
    box-shadow: 0 0 0 3px rgba(33, 233, 50, 0.2);
}

.timeline ul li h3 
{
    margin: 0;
    padding: 0;
    font-weight: bold;
    color: rgba(33, 233, 50, 1);
}

.timeline ul li p
{
    margin: 10px 0 0;
    padding: 0;
}

.timeline ul li .time h4
{
    margin: 0;
    padding: 0;
    font-size: 14px;
}

.gold-highlight
{
    color: rgb(255, 153, 0);
    font-weight: bold;
}